* {
  margin: 0;
  padding: 0;
}

:root {
  --blue: #00acdc;
}

.body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.nav_item {
  text-transform: uppercase;
}

.logo_link {
  width: 40px;
  height: 40px;
}

.nav_link {
  color: #000;
  align-items: center;
  height: 60px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
}

.nav_list {
  list-style: none;
  display: flex;
}

.nav_item:not(:last-child) {
  margin-right: 20px;
}

.is-active {
  color: var(--blue);
  position: relative;
}

.nav_link:hover {
  color: var(--blue);
}

.is-active:after {
  content: "";
  background-color: var(--blue);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

